<template>
  <div id="root">
    <header>
    </header>
    <nav>
      <ul>
        <li v-for="(one, i) in navigator" :key="i">
          <router-link :to="one.path">{{ one.name }}</router-link>
        </li>
      </ul>
    </nav>
    <main>
      <br>
        <router-view></router-view>
      <br>
      <section>
        <article>
          <span>It's "{{ base.isScreenDark ? 'DARK' : 'LIGHT' }}" mode.</span>
          <button class="btn" @click="changeScreenMode()">Change</button>
        </article>
      </section>
    </main>
    <aside></aside>
    <footer>
      <br>
      <ul>
        <li>
          <a href="mailto:idealful@gmail.com">Contact me</a>
        </li>
      </ul>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      base: {
        isScreenDark: false,
      },
      navigator: [
        { name: 'Home', path : '/' }, 
        { name: 'About', path : '/about' }
      ],
    }
  },
  methods: {
    changeScreenMode() {
      this.base.isScreenDark = !this.base.isScreenDark;

      const elemBody = document.body;
      if (this.base.isScreenDark) {
        elemBody.classList.add('dark');
      } else {
        elemBody.classList.remove('dark');
      }
    },
  },
}
</script>

<style src="./assets/css/root.css"></style>
<style src="./assets/css/nav.css"></style>
